























.fl{
	float: left;
}
.fr{
	float: right;
}
.header-top{
	background: #007e40;
	padding: 5px 0;
}
.top-left >div{
	float: left;
	margin-right: 15px;
}
.top-left >div i{
	color: #faba4b;
}
.header-top span,.top-right >div i,.top-right >div a{
	color: #fff;
}
.top-right{
	float: right;
}
.top-right >div{
	float: left;
	margin-left: 10px;
}
.top-right >div i{
	cursor: pointer;
	margin-right: 5px;
}
.top-right >div:nth-of-type(2){
	position: relative;
}
.wechatQ{
	width: 110px;
    height: 110px;
    margin: 0;
    position: absolute;
    top: 30px;
    left: -40px;
    border: 1px solid #999;
    z-index: 1;
    opacity: 0;
	visibility: hidden;
    -webkit-transition: all .25s ease .1s;
    transition: all .25s ease .1s;
    transform: translate3d(0px, 10px, 0px);
    -moz-transform: translate3d(0px, 10px, 0px);
    -webkit-transform: translate3d(0px, 10px, 0px);
}
.wechatQ span{
    width: 0;
    height: 0;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #fff;
    border-left: 10px solid transparent;
    position: absolute;
    left: 40px;
    top: -10px;
    z-index: 1;
}
.wechatQ img{
	margin: 4px;
}
.top-right >div i:hover +.wechatQ{
	opacity: 1;
    visibility: visible;
    transform: translate3d(0px, 0px, 0px);
    -moz-transform: translate3d(0px, 0px, 0px);
    -webkit-transform: translate3d(0px, 0px, 0px);
}
.logo img{
	margin: 30px 0;
}
.logo img:nth-of-type(2){
	margin: 53px 0;
}
.pc-nav{
	background: #fff;
	border-bottom: 3px solid #e3aa20;
	border-top: 1px solid #ebebeb;
}
.pc-nav ul{
	margin: 0;
}
.first-nav>li{
	width: 11%;
	border-left: 1px solid #e4e4e4;
	position: relative;
}
.first-nav>li:last-of-type{
	border-right: 1px solid #e4e4e4;
}
.first-nav>li a{
	display: block;
	padding: 15px 0;
	color: #333;
}
.first-nav>li.active{
	background:#13a35d;
}
.first-nav>li.active a{
	color: #fff;
}
.hover{
    opacity: 1 !important;
    visibility: visible !important;
    transform: translate3d(0px, 0px, 0px) !important;
    -moz-transform: translate3d(0px, 0px, 0px) !important;
    -webkit-transform: translate3d(0px, 0px, 0px) !important;
}
.second-nav{
	width: 250px;
	position: absolute;
	top: 50px;
	left: -0px;
	background: rgba(30,141,88,.9);
	z-index: 2;
	opacity: 0;
	visibility: hidden;
    -webkit-transition: all .25s ease .1s;
    transition: all .25s ease .1s;
    transform: translate3d(0px, 10px, 0px);
    -moz-transform: translate3d(0px, 10px, 0px);
    -webkit-transform: translate3d(0px, 10px, 0px);
}
.second-nav li{
	border-bottom: 1px solid #51c28c ;
}
.second-nav li:last-of-type{
	border-bottom: 0;
}
.second-nav a{
	color: #fff !important;
	padding: 10px 3px;
	font-size: 14px;
	cursor: pointer;
}
.nav-mask {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity 0.4s,visibility 0.4s;
    transition: opacity 0.4s,visibility 0.4s;
}
.nav-mask.active {
    visibility: visible;
    opacity: 0.6;
}
.nav-active {
    -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.8);
    left: 50% !important;
}
.m-nav{
	background: #005A55;
	position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    -webkit-box-shadow: none;
            box-shadow: none;
   	left: 100%;
    -webkit-transition: all 0.4s,box-shadow 0.4s;
    -webkit-transition: all 0.4s,-webkit-box-shadow 0.4s;
    transition: all 0.4s,-webkit-box-shadow 0.4s;
    transition: all 0.4s,box-shadow 0.4s;
    transition: all 0.4s,box-shadow 0.4s,-webkit-box-shadow 0.4s;
    max-width: 50%;
}
.m-nav ul{
	padding: 0;
	list-style: none;
}
.m-nav li{
	margin: 10px 0;
	border-bottom: 1px solid #00726c;
	position: relative;
	left: 100%;
}
.move{
	left: 0 !important;
}
.m-nav li:first-of-type{
	-webkit-transition: all ease-in-out .3s;
    transition: all ease-in-out .3s;
}
.m-nav li:nth-of-type(2){
	-webkit-transition: all ease-in-out .4s;
    transition: all ease-in-out .4s;
}
.m-nav li:nth-of-type(3){
	-webkit-transition: all ease-in-out .5s;
    transition: all ease-in-out .5s;
}
.m-nav li:nth-of-type(4){
	-webkit-transition: all ease-in-out .6s;
    transition: all ease-in-out .6s;
}
.m-nav li:nth-of-type(5){
	-webkit-transition: all ease-in-out .7s;
    transition: all ease-in-out .7s;
}
.m-nav li:nth-of-type(6){
	-webkit-transition: all ease-in-out 0.8s;
    transition: all ease-in-out 0.8s;
}
.m-nav li:nth-of-type(7){
	-webkit-transition: all ease-in-out 0.9s;
    transition: all ease-in-out 0.9s;
}
.m-nav li:nth-of-type(8){
	-webkit-transition: all ease-in-out 1.1s;
    transition: all ease-in-out 1.1s;
}
.m-nav li:nth-of-type(9){
	-webkit-transition: all ease-in-out 1.1s;
    transition: all ease-in-out 1.1s;
}
.m-nav li:nth-of-type(10){
	-webkit-transition: all ease-in-out 1.3s;
    transition: all ease-in-out 1.3s;
}
.m-nav li a{
	width: 100%;
	display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.m-nav span,.m-nav i{
	display: inline-block;
	color: #fff;
	padding: 10px 0;
}
.m-nav i{
	width: 15%;
	font-size: 20px;
	text-align: center;
}
.m-nav>ul >li>a>i:first-of-type{
	padding-left: 10px;
}
.m-nav>ul >li>a>i:nth-of-type(2){
	color: #515b60;
}
.m-nav span{
	width: 70%;
	font-size: 16px;
    margin-left: 15px;
}
.m-second-nav{
	background: #04403c;
	display: none;
}
.m-nav .active{
	display: block;
}
.m-second-nav li{
	margin: 0;
	border-bottom: 1px solid #005a54;
}
.m-second-nav li:last-of-type{
	border: 0;
}
.m-second-nav span{
	margin-left: 15%;
}
